import Vue from 'vue'
import Router from 'vue-router'
import login from '@/pages/login.vue'
import register from '@/pages/register.vue'
import reset from '@/pages/reset.vue'
import admin from '@/pages/admin.vue'
import paper from '@/pages/paper.vue'
import account from '@/pages/account.vue'
import paperCreate from '@/pages/paperCreate.vue'
import paperTitle from '@/pages/paperTitle.vue'
import category from '@/pages/category.vue'
import categoryCreate from '@/pages/categoryCreate.vue'
import categoryTitle from '@/pages/categoryTitle.vue'
import categoryEdit from '@/pages/categoryEdit.vue'
import report from '@/pages/report.vue'

Vue.use(Router)

const router = new Router({ 
  mode: 'hash',
  routes: [
    { path: '/', redirect: '/login' },
    { path: '/login', component: login, name:'' }, 
    { path: '/register', component: register, name:'' }, 
    { path: '/reset', component: reset, name:'' }, 
    { path: '/admin', component: admin, name:'' }, 
    { path: '/admin/account', component: account, name:'' }, 
    { path: '/admin/paper', component: paper, name:'' }, 
    { path: '/admin/paper/create/:id', component: paperCreate, name:'' }, 
    { path: '/admin/paper/title/:id', component: paperTitle, name:'' }, 
    { path: '/admin/category/:id', component: category, name:'' }, 
    { path: '/admin/category/create/:id', component: categoryCreate, name:'' }, 
    { path: '/admin/category/title/:id', component: categoryTitle, name:'' }, 
    { path: '/admin/category/edit/:id', component: categoryEdit, name:'' }, 
    { path: '/admin/report/:id', component: report, name:'' }, 
  ]
})

router.beforeEach( async(to, from, next) => {

	// if( !token ) {
	// 	this.$router.push('/login')
	// }

  document.title = to.name

  next()
})

export default router



